	module.exports = {"data":{"post":{"html":"<p>Now that I'm taking the train to work, I have time to read things like <a title=\"Die Zeit 09 / 2004 - Hamann/Perger: Die Angst-Macher\" href=\"http://www.zeit.de/2004/09/03_Populismus\">an article on populism and the Bild newspaper</a> in Die Zeit. It makes a number of good points. Bild is now the most important media outlet <em>(Leitmedium)</em> in Germany, having replaced Der Spiegel. True... if I were to imagine a federal minister today ordering a police raid of a newsroom, <a title=\"Spiegel scandal - Wikipedia\" href=\"http://en.wikipedia.org/wiki/Spiegel_scandal\">à la Franz-Josef Strauss</a>, it would be at Bild, not Spiegel. Bild's editorial line (against health reform and pension reform) reflects to a large degree its older, lower-income readership, a clientele it shares with the SPD. But while the paper can strengthen trends, it cannot set them. It was unable to elect Stoiber, who it heavily supported in 2002. In the end the paper, whose circulation of 3.8 million is at a long-time low, is not trying to set a political agenda, but trying to cement its readership by playing on its fear of reform.</p>","fields":{"title":"The Fear Mongers","date":"22 Feb 2004","url":"https://papascott.de/2004-02-22-the-fear-mongers/"}}},"pathContext":{"slug":"/2004-02-22-the-fear-mongers/","prev":{"fields":{"title":"Let Him Run","date":"22 Feb 2004","slug":"/2004-02-22-let-him-run/"}},"next":{"fields":{"title":"Blame Janet Jackson","date":"23 Feb 2004","slug":"/2004-02-23-blame-janet-jackson/"}}}}

